import React from 'react';
import Button from '@material-ui/core/Button';

class Boxcover2 extends React.Component {
    render(){

        const style={
                width:'auto',
                marginBottom:'-25px',
                padding:'20px',
                background: 'url(https://images.unsplash.com/photo-1562177617-8a133331c4fb?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80) center / cover',
                color:'white',
                backgroundSize:'cover'

        }
        return(
            <div style={style}>
            <h1> Vr has changed the business landscape of technology </h1>   
            <a href='/contact' style={{textDecoration:'none'}}>
                <Button variant="outlined" style={{width:'auto', height:'30px',borderColor:'orange',color:'white'}}>Get in touch</Button>
           </a>
            </div>

        )
    }
}

export default Boxcover2