import React, {Component} from 'react';
import Navmenu from '../layout/Navmenu';
//import Slider from '../containers/experiences/slider';
// import Mining from '../containers/experiences/mining';
// import Box from '../containers/experiences/box';
// import Safety from '../containers/experiences/safety';
// import Medicine from '../containers/experiences/box2';
import Footer from '../components/footer';
import Miningcard from '../containers/experiences/cards/mining';
import Jobcard from '../containers/experiences/cards/jobsimulation';
import Tourism from '../containers/experiences/cards/tourism';
import Healthcard from '../containers/experiences/cards/health';
import Showcase from '../containers/experiences/cards/showcase';
import Websites from '../containers/experiences/cards/website';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';


class Experience extends Component {
    render(){
        const style ={
            width:'100%',
            textAlign:'center',
            display:'inline-block',
            justifyContent:'center'
            
        }

        const section = {
            display:'inline-block',
           //  padding:'20px', 
            // background:'url(https://images.unsplash.com/photo-1478416272538-5f7e51dc5400?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=672&q=80)',
             width:'100%',
            textAlign:'center',
            justifyContent:'center'}

        const section2 ={
            width:'100%',
            //left:'100%',
            display:'inline-block',
           // marginLeft:'700px',
            marginBottom:'200px',
            paddingTop:'100px',
            textAlign:'center',
          //  background:'url(https://images.unsplash.com/photo-1514499007249-cd680c1d1060?ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80) center / cover'
        
        }

        return (
            <div>
                <Navmenu/>
                <div style={style}>
                    <section style={section} >
                <h1 style={{paddingBottom:'-1000px', color:'black'}}>Our Solutions <br></br>Work well in the following industries</h1>
                </section> 
                 <p style={{color:'grey'}}>but not limited to</p>
                <section style={{display:'inline-flex',textAlign:'center', margin:'10px'}}>
                 <Miningcard/>
                </section>
                <section style={{display:'inline-flex',textAlign:'center', margin:'10px'}}>
                 <Jobcard/>
                </section>
                <section style={{display:'inline-flex',textAlign:'center', margin:'10px'}}>
                 <Tourism/>
                </section>
                <section style={{display:'inline-flex',textAlign:'center', margin:'10px'}}>
                 <Showcase/>
                </section>
                <section style={{display:'inline-flex',textAlign:'center', margin:'10px'}}>
                 <Healthcard/>
                </section>
                <section style={{display:'inline-flex',textAlign:'center', margin:'10px'}}>
                 <Websites/>
                </section>

                
                </div>
                <div style={section2}>
        <p style={{paddingTop:'50px', color:'grey'}}> don't miss the opportunity</p>
        <h2 style={{color:'black'}}>Tell us your line of business <br/> We'll introduce you to virtual reality </h2>
        <form>
            <fieldset style={{border:'1px solid orange',display:'inline-block',padding:'20px',marginBottom:'10px'}}>
            <legend style={{color:'black'}}>Leave us your details and we'll give you a call</legend>
       
        <TextField
        id="outlined-dense"
        label="Name"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}

      /><br/>
            <TextField
        id="outlined-dense"
        label="Phone"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}
      /><br/>
            <TextField
        id="outlined-dense"
        label="Line of Business"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}
      /><br/>
            <Button type='submit' variant="outlined" style={{margin:'10px',borderColor:'grey'}}>Submit</Button>
            </fieldset>
        </form>
        </div>
                
                <Footer/>
                
        </div>
        );
    }
}

export default Experience;