import React from 'react';
import Button from '@material-ui/core/Button';
import logo from '../assets/images/logo.png';


class LandingPage extends React.Component {

    render(){
        const style ={
           'background':'url(https://live.oculusvr.com/assets/images/search_hero.jpg) no-repeat center center fixed',
            backgroundSize:'cover',
           // backgroundRepeat:'no-repeat',
            paddingBottom:'250px',
           // justifyContent:'center',
           // backgroundPosition:'center',
            color:'white',
            width:'auto',
            height:'100%',
            display:'inline-box',
            overflow:'hidden',
            '-o-background-size':'cover',
            '-moz-background-size':'cover',
            '-webkit-background-size':'cover',

           // marginTop:'-28px'
        }

        const section = {
           // margin: '0',
           // position: 'absolute',
           top: '50%',
            left: '50%',
            textAlign:'center',
           // display:'inline-block',
            justifyContent:'center',
            width:'auto',
           
        }

        const p ={
            font:'normal 16px/normal "Times New Roman", Times, serif',
            fontSize:'12px',
            color:'grey'

        }

        const b ={
          //  margin:'10px',
            color:'white',
           // marginTop:'100px',
            borderColor:'blue'

        }

        const img ={
            width:'150px',
           // top: '50%',
           paddingTop:'15%'
        }

        // const b2={
        //     color:'grey'
        // }
        const a ={
          //  margin:'10px',
            marginBottom:'12px',
            textDecoration: 'none',
            marginTop:'12px'
        }
        


        return(
            <div style ={style}>
                <section style={section}>
               
                <img src={logo} alt={logo} style={img}></img>    
                <p style={p}>it's all in the detail</p>
                </section>
                <section style={{justifyContent:'center', textAlign:'center',}}>
                <a href={`${process.env.PUBLIC_URL}/home`} style={a}><Button variant="outlined" style={b}>Normal Website View</Button></a>
                <a href= 'https://hoggervr.github.io/virtualsite/' style={a}><Button variant="outlined" style={b}>Virtual Reality Website</Button></a>
                </section>
            </div>
        )
    }
}

export default LandingPage;
