import React, {Component} from 'react';
import Navmenu from '../layout/Navmenu';
import Boxcover from '../containers/home/boxcover';
import Blackcover from '../containers/home/blackcover';
import Button from '@material-ui/core/Button';
import Boxcover2 from '../containers/home/boxcover2';
import Footer from '../components/footer';
//import Quest from '../assets/images/quest.png';
import TextField from '@material-ui/core/TextField';
// import Radium, {StyleRoot} from 'radium';


class Home extends Component {
    render(){
        // const stylemain={
        //     '@media (min-width:500px)':{
        //         width:'auto'
        //     }
        // }

        const style = {
            width:'100%',
            textAlign:'center',
            background:'url(/src/assets/images/logo.png)'
        }

        const p = {
            color:'grey',
            paddingTop:'40px',
            marginBottom:'-30px'
        }

        const h1 ={
            paddingBottom:'10px',
    //        fontSize:'50px'
        }

        const h5={
            paddingBottom:'30px',
            width:'auto',
            textAlign:'center'
        }
         const b ={
            margin:'10px',
           // color:'white',
          //  margin:'-40px',
            //background:'blue',
           // boxShadow:'0px 4px 10px black',
            borderColor:'orange'
        }

        const layout1 ={
            backgroundImage:'url(../quest.png) center / cover',
           // backgroundColor:'red' 
        }

        const layout2={
            //display:'inline-flex',
         //   marginBottom:'40px',
           // background:'url(https://images.pexels.com/photos/1059115/pexels-photo-1059115.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)',
            backgroundFilter: 'blur(2px)',
            backgroundSize:'cover',
            width:'auto',
            display:'inline-flex'
        }

        const section={
            width:'auto',
           // left:'100%',
            display:'inline-block',
            //marginLeft:'700px',
            marginBottom:'20px',
           // paddingTop:'100px',
            color:'white'
        }
                
        return (
            
            <div > 
                <Navmenu/>
            <div style={style}>
                
                <div style={layout1}>
        <p style={p}>Want to be part of the next business revolution?</p>
        <h1 style={h1}>Virtual reality can elevate<br/> your business</h1>
        
        <a href='/contact' style={{textDecoration:'none'}}><Button  variant="outlined" component="span"  style={b}>Get me started in VR</Button></a>
        <h5 style={h5}>Engage your employees, customers and clients to a world of possibilites with HoggerVr </h5>
        </div>
        <hr style={{width:'35%',display:'inline-block',background:'blue',boxShadow:'0px 4px 10px black'}}></hr>
        <div>
        <Boxcover/>
        <Blackcover/>
        </div>
        <div style={layout2}>
        
        <section style={section}>
        <p style={{paddingTop:'50px', color:'grey'}}> don't miss the opportunity</p>
        <h2 style={{color:'black',width:'auto'}}>Not exactly sure how Virtual Reality can <br/> Change your business? </h2>
        <form>
            <fieldset style={{border:'1px solid orange',display:'inline-block',padding:'20px',marginBottom:'10px'}}>
            <legend style={{color:'black'}}>Leave us your details and we'll give you a call</legend>
       
        <TextField
        id="outlined-dense"
        label="Name"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}

      /><br/>
            <TextField
        id="outlined-dense"
        label="Phone"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}
      /><br/>
            <TextField
        id="outlined-dense"
        label="Line of Business"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}
      /><br/>
            <Button type='submit' variant="outlined" style={{margin:'10px',borderColor:'grey'}}>Submit</Button>
            </fieldset>
        </form>
        </section>
        
        </div>
        <Boxcover2/>
        <Footer/>
    </div>
    </div>
    
   
        
        );
    }
}

export default Home;