import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});

export default function Mining() {
  const classes = useStyles();

  return (
    <Card className={classes.card} style={{display:'inline-block'}}>
      <CardActionArea style={{background:'url(https://images.pexels.com/photos/1743227/pexels-photo-1743227.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500) center / cover'}}>
        <CardMedia
          className={classes.media}
          image="/src/assets/images/hogger.jpg"
          title="Contemplative Reptile"
        />
        <CardContent style= {{}}>
          <Typography gutterBottom variant="h3" component="h2" style={{color:'black'}}>
            Vr Experiences
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p" style={{color:'black'}}>
            Our scope is not limited to any field or technology. We produce 3d models, 360 videos and images for 
            any need you may have as a client. Whether you major in tourism, real estate or entertainment, HoggerVr
            has a portion for you in virtual reality. We work to give everybody in your busines circle, a part of
            story that has not been told before. Give us a call and we'll be sure to partner with you
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Visit Vr
        </Button>
        <a href='/experiences' style={{textDecoration:'none'}}>
        <Button size="small" color="primary">
          Learn More
        </Button>
        </a>
      </CardActions>
    </Card>
  );
}
