import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});

export default function Experiences() {
  const classes = useStyles();

  return (
    <Card className={classes.card} style={{display:'inline-block'}}>
      <CardActionArea style={{background:'url(https://images.pexels.com/photos/373076/pexels-photo-373076.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) center / cover'}}>
        <CardMedia
          className={classes.media}
          image="/src/assets/images/hogger.jpg"
          title="Contemplative Reptile"
        />
        <CardContent style= {{}}>
          <Typography gutterBottom variant="h3" component="h2">
            Virtual Showcasing
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p" style={{color:'black'}}>
            Showcase your business at event's and functions with our technology.
            HoggerVr works to produce the best working models for your business, by telling your story in a 6D  
            visual showcase. We have expreince and expertise in producing the best 6d visuals, from games to movies.
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      
         <Button size="small" color="accent" variant="outlined">
          Visit Vr
        </Button>
        
        <a href='/experiences' style={{textDecoration:'none'}}>
        <Button size="small" color="primary" variant="outlined">
          Learn More
        </Button>
        </a>
        
      </CardActions>
    </Card>
  );
}
