import React from 'react';
import Cards from '../home/cards';
import Button from '@material-ui/core/Button';

class Blackcover extends React.Component{
    render(){
        const style ={
            width:'auto',
            textAlign:'center',
            backgroundColor:'#333333',
            color:'white',
            padding:'20px',

        }
        const b ={
            margin:'10px',
            color:'white',
            height:'50px',
            width:'350px',
          //  margin:'-40px',
           // background:'blue',
           // boxShadow:'0px 4px 10px black'
           borderColor:'yellow '
        }

        return (
            <div style ={style}>
                <h4>Experience is our definition.<br/> Immerse your business to the level of your dreams</h4>
                <p style={{color:'grey'}}> Here is what we can do to your business with Vr</p>
                <Cards/>
                <br></br><br></br>
                <a href='/contact' style={{textDecoration:'none'}}>
                <Button href='/home' variant="outlined" component="span"  style={b}>Get me started in VR</Button>
                </a>
                <h1>We believe in an elevated visual experience</h1>
            </div>
        );
    }
}

export default Blackcover