import React from 'react';
import {Layout,Header,Navigation,Content,Drawer} from 'react-mdl';
import logo from '../assets/images/logo.png';
// defualt header for full website
// import Radium from 'radium';

class Navmenu extends React.Component{
    render(){

        const img = {
            height:'100px',
            padding:'20px',
            marginTop:'50px',
            marginRight:'600px',
        }

        const nav={
           // float:'right',
            marginRight:'100px',
            color:'red'
        }

        const layout={
            height: '200px',
            position: 'relative',
            width:'auto'
            // '@media (min-width:500px)':{
            //     width:'100%',
            // }
        }


        return(
            
            <div style={layout}>
    <Layout 
    fixedHeader
    style={{background: 'url(https://www.oculusforbusiness.com/app/uploads/2019/04/get-started-image-1.jpg) center / cover',width:'100%'}}>
        <Header
        seamed
        scroll
        transparent title="" style={{color: 'white'}}>
            <a href='/normalsite/home'><img src={logo} style={img} alt='logo'/></a>
            <div style={nav}>
            <Navigation >
                <a href="/normalsite/home">Home</a>
                <a href="/normalsite/experiences">Experiences</a>
                <a href="/normalsite/contact">Contact</a>
                <a href="/normalsite/about">About Us</a>
            </Navigation>
            </div>
        </Header>
       
        <Drawer title="HoggerVR" style={{color:'black',textAlign:'center'}}>
            <Navigation style={{color:'red',backgroundColor:'black'}}>
            <a href="/normalsite/home">Home</a>
                <a href="/normalsite/experiences">Experiences</a>
                <a href="/normalsite/contact">Contact</a>
                <a href="/normalsite/about">About Us</a>
            </Navigation>
        </Drawer>
    
        <Content/>
    </Layout>
</div>
        );
    }
}

export default Navmenu;