import React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Mining from '../Cards/Mining'
import Training from '../Cards/Training';
import Experiences from '../Cards/Expereinces';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: 500,
  },
}));

export default function Cards() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  function handleChange(event, newValue) {
    setValue(newValue);
  }

  function handleChangeIndex(index) {
    setValue(index);
  }

  
  return (
    <div className={classes.root} style={{display:'inline-block',width:'auto'}}>
      <AppBar position="static" color="default" style={{backgroundColor:'#333333',borderTop:'1px solid orange',borderLeft:'1px solid orange',borderRight:'1px solid orange'}}>
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          //textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
          style={{color:'white'}}
        >
          <Tab label="Showcasing" {...a11yProps(0)}  />
          <Tab label="Training" {...a11yProps(1)} />
          <Tab label="Experiences" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
        style={{color:'black', backgroundColor:'#333333'}}
      >
        <TabPanel value={value} index={0} dir={theme.direction} >
        <Experiences/>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <Training/>
        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
        <Mining/>
        </TabPanel>
      </SwipeableViews>
    </div>
  );
}
