import React, {Component} from 'react';
import Navmenu from '../layout/Navmenu';
import Footer from './footer';
import Box from '../containers/Aboutus/box';
import Box2 from '../containers/Aboutus/box2';
import Whyhoggervr from '../containers/Aboutus/whyhoggervr';


class About extends Component {
    render(){
        
        const style = {
            width:'auto',
            textAlign:'center',
        }
        
        
        return (
            <div style ={style}>
                 <Navmenu/>
        <Box/>
        <hr></hr>
        <Box2/>
        <hr></hr>
        <Whyhoggervr/>
        <Footer/>
        </div>
        );
    }
}

export default About;