import React from 'react';
import { Button } from '@material-ui/core';
//import quest from '../../assets/images/quest.png'


class Box extends  React.Component{
    render(){

        const style= {
           // display:'inline-flex',
            width:'100%',
            justifyContent:'center',
           // background:'url(https://images.unsplash.com/photo-1541587943171-46b1eacd11fa?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80)',
            display:'inline-flex',
            textAlign:'center',
            color:'black'         
        }
        const b ={
            margin:'10px',
           // color:'white',
          //  margin:'-40px',
            //background:'blue',
           // boxShadow:'0px 4px 10px black',
            borderColor:'grey'
        }
        return(
            <div style={style}>
                <section style={{padding:'50px'}}>
                    <h3>Who are we?</h3>
                <p>HoggerVR is a company that offers Virtual Reality Solutions. 
                    Our Aim is to help Companies achieve more<br/> with little by providing 
                    simulated environments 
                    that allow people to experience the real world in a safe and interactive environment</p>
                <a href='/contact' style={{textDecoration:'none'}}><Button  variant="outlined" component="span"  style={b}>Get me started in VR</Button></a>
                </section>
                  
            </div>
        );

    }
}

export default Box