import React from 'react';

class Footer extends React.Component {
    render(){

        const style = {
            backgroundColor:'black',
            color:'white',
            textAlign:'center',
            width:'auto'
        }
        return(
            <div style={style}>
            <h1>HoggerVR</h1>
            <hr></hr>
            2019 all rights reserved 
            
            </div>
        );

    }
}

export default Footer