import React from 'react';
import Button from '@material-ui/core/Button';

class Box2 extends  React.Component{
    render(){

        const style= {
           // display:'inline-flex',
            width:'100%',
            justifyContent:'center',
           // background:'url(https://images.unsplash.com/photo-1541587943171-46b1eacd11fa?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80)',
            display:'inline-flex',
            textAlign:'center',
            color:'black'         
        }
        const b ={
            margin:'10px',
           // color:'white',
          //  margin:'-40px',
            //background:'blue',
           // boxShadow:'0px 4px 10px black',
            borderColor:'grey'
        }
        return(
            <div style={style}>
                <section style={{padding:'50px'}}>
                    <h3>What do we do?</h3>
                <p>HoggerVR is a company that focuses on virtual technology<br/> 
                   Our work is comprised of creating simulated environments, that allow real<br/>
                   world experiences in controlled environments. Our expertise involves 3d simulations, 360<br/>
                   degree videos, animation and virtual technology programming
                    </p>
                <a href='/contact' style={{textDecoration:'none'}}><Button  variant="outlined" component="span"  style={b}>Get me started in VR</Button></a>
                </section>
                  
            </div>
        );

    }
}

export default Box2