import React, {Component} from 'react';
import Navmenu from '../layout/Navmenu';
import Footer from '../components/footer';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

class Contact extends Component {
    render(){

        const style = {
            width:'auto',
            textAlign:'center',
           // display:'inline-block'
        }
        
        return (
            <div style ={style}>
                <Navmenu/>
                <div style={{display:'inline-block'}}>
                    <section style={{padding:'20px', justifyContent:'center'}}>
                        <h3>Get in Touch</h3>
                        <h5 style={{color:'grey',fontSize:'15px'}}>Leave us a message and we'll be glad to help you
                            immerse virtual reality into your business.
                        </h5>
                    <form style={{justifyContent:'center',display:'inline-block'}}>
            <fieldset style={{border:'1px solid orange',display:'inline-block',padding:'20px',marginBottom:'10px'}}>
            <legend style={{color:'black'}}>Leave us your details and we'll give you a call</legend>
       
        <TextField
        id="outlined-dense"
        label="First Name/Company Name"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange',paddingRight:'10px'}}
              

      />
       
      <TextField
        id="outlined-dense"
        label="Last Name"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}
        />
      
      <br/>
            <TextField
        id="outlined-dense"
        label="Phone"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange',paddingRight:'10px'}}
      />
            <TextField
        id="outlined-dense"
        label="Line of Business"
        margin="dense"
        variant="outlined"
        style={{width:'350px',borderColor:'orange'}}
      /><br/>
       <TextField
        id="standard-multiline-flexible"
        label="Leave us your message here"
        multiline
        rowsMax="10"
        //value={values.multiline}
       // onChange={handleChange('multiline')}
       // className={classes.textField}
        margin="normal"
        style={{width:'300px'}}
      />
      <br/>
            <Button type='submit' variant="outlined" style={{margin:'10px',borderColor:'grey'}}>Submit</Button>
            </fieldset>
        </form>
                    </section>
                   
            </div>
                <Footer/>
        </div>
        );
    }
}

export default Contact;