import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});

export default function Training() {
  const classes = useStyles();

  return (
    <Card className={classes.card} style={{display:'inline-block'}}>
      <CardActionArea style={{background:'url(https://images.pexels.com/photos/1078879/pexels-photo-1078879.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) center / cover'}}>
        <CardMedia
          className={classes.media}
          image="/src/assets/images/hogger.jpg"
          title="Contemplative Reptile"
        />
        <CardContent style= {{}}>
          <Typography gutterBottom variant="h3" component="h2" style={{color:'black'}}>
            Vr Training
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p" style={{color:'black'}}>
            Speed up business by using great technologies in virtual reality powered by hoggerVr. Our expertise in extended to
            mining, factories, construction,medium to large enterprises and packaging just to mention a few. We work with a resilience 
            to immerse your business to its full potential.In Vr training, your business can be sped up to 300% faster. Contact us now for a free quote  
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Visit Vr
        </Button>
        <a href='/experiences' style={{textDecoration:'none'}}>
        <Button size="small" color="primary">
          Learn More
        </Button>
        </a>
      </CardActions>
    </Card>
  );
}
